* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

input {
  margin-bottom: 30px;
  margin-top: 30px;
  margin-left: 50px;
}

.country-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  list-style: none;
}
.item {
  display: flex;
  align-items: center;
  font-size: 25px;
  font-weight: 500;
}
img {
  margin: 10px;
}
.country-heading {
  display: flex;
  align-items: center;
}
.info {
  font-size: 25px;
  font-weight: 600;
}
span {
  font-weight: 400;
}
